
.hljs {
  display: block;
  overflow-x: auto;
  color: #525252;
  padding: 15px;
  -webkit-text-size-adjust: none;
  font-size: 16px;

}

.hljs-doctype {
  color: #999
}

.hljs-tag {
  color: #3e76f6
}

.hljs-attr {
  color: #e96900
}

.hljs-value {
  color: #42b983
}

.hljs-keyword {
  color: #e96900
}

.hljs-string {
  color: #42b983
}

.hljs-comment {
  color: #b3b3b3
}

.hljs-operator .hljs-comment {
  color: #525252
}

.hljs-regexp {
  color: #af7dff
}

.hljs-built_in {
  color: #2db7f5
}

.css .hljs-class {
  color: #e96900
}

.css .hljs-number, .javascript .hljs-number {
  color: #fc1e70
}

.css .hljs-attribute {
  color: #af7dff
}

.css .hljs-important {
  color: red
}

.actionscript .hljs-literal, .javascript .hljs-literal {
  color: #fc1e70
}

pre {
  padding: 0;
  margin: 0
}

code {
  display: inline-block;
  background: #f7f7f7;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  margin: 0 3px;
  padding: 1px 5px;
  border-radius: 3px;
  color: #666;
  border: 1px solid #eee
}

pre code {
  display: inline;
  margin: 0;
  padding: 0;
  border: none;
  background: transparent
}

pre.bg code {
  background: #f7f7f7
}

.bg-cover {
  background: no-repeat center;
  background-size: cover;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-666 {
  color: #666;
}

.text-999 {
  color: #999;
}

/*flex*/
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.flex-between {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.flex-center {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.flex-auto {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}

.flex-none {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
